import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';


import { AnnouncementsComponent } from './announcements.component';

const routes: Routes = [
  // { path: '', component: AnnouncementsComponent },
  {
    path: '', component: AnnouncementsComponent,
    children: [
      { path: 'user-group-based-announcement', loadChildren: () => import('src/app/modules/user/announcements/user-group-announcement/user-group-announcement.module').then(m => m.UserGroupAnnouncementModule) },
      { path: 'user-interest-based-announcement', loadChildren: () => import('src/app/modules/user/announcements/user-interest-announcement/user-interest-announcement.module').then(m => m.UserInterestAnnouncementModule) },
      { path: '**', redirectTo: 'user-group-based-announcement', pathMatch: 'full' }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AnnouncementsRoutingModule { }
